.Navbar {
    border-bottom: 1px solid var(--invert-color);
    background-image: linear-gradient(to bottom right, #6CAFCC, #7EADC2);
    padding: 0% 10%;
    height: 60px;
}

.Navbar .grid-container {
    display: grid;
    grid-template-columns: 20% 20% 20% 20% 20%;
    list-style-type: none;
    margin: 0;
    padding: 0;
    height: 100%;
}
.Navbar .grid-container li {
    font-size: 30px;
    height: fit-content;
    margin: auto auto;
}
.Navbar .grid-container li a {
    text-decoration: none;
}
.Navbar .grid-container li a:link {
    color: #5D808F;
}
.Navbar .grid-container li a:visited {
    color: #173542;
}
.Navbar .grid-container li a:hover {
    color: white;
    transition-duration: 0.2s;
}
.Navbar .grid-container button {
    display: flex;
    justify-content: center;
    align-items: center;
}
.Navbar .grid-container li:hover {
    transform: scale(1.2, 1.2);
    transition-duration: 0.3s;
}
  
/* Switch - https://codepen.io/wilsonpage/pen/cxKEs */
.switch {
    display: inline-block;
    font-size: 25px;
    height: 1em;
    width: 2em;
    background-color: var(--main-bg-color);
    border-radius: 1em;
}
.switch input {
    position: absolute;
    opacity: 0;
}
.switch div {
    height: 1em;
    width: 1em;
    border-radius: 1em;
    background-color: var(--container-bg-color);
    box-shadow: 0 0.1em 0.3em rgba(0,0,0,0.3);
}
.switch input:checked + div {
    -webkit-transform: translate3d(100%, 0, 0);
       -moz-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}


@media only screen and (max-width: 600px) {
    .Navbar .grid-container li {
        font-size: 25px;
    }
}
@media only screen and (max-width: 500px) {
    .Navbar {
        padding: 0% 1%;
    }
}
@media only screen and (max-width: 400px) {
    .Navbar .grid-container li {
        font-size: 20px;
    }
}
